const checkbox = document.querySelector("input[gift-wrap]");
if (!checkbox) return;

let hasGiftInCart = false;
let cart;
document.addEventListener("DOMContentLoaded", async function () {
  cart = await getCart();
  const productId = checkbox.getAttribute("giftwrap-variant");

  for (const item of cart.items) {
    if (item.variant_id == productId) {
      checkbox.checked = true;
      hasGiftInCart = true;
    }
  }
});

checkbox.addEventListener("change", async function () {
  productId = this.getAttribute("giftwrap-variant");

  if (this.checked) {
    if (!hasGiftInCart) {
      await addProductToCart(productId);
    }
  } else {
    await removeProductFromCart(productId);
  }

  cart = await getCart();
  const format = window.theme.settings.money_with_currency_format;
  const cartTotalPrice = formatMoney(cart.total_price, format);

  document.querySelector("#cart-drawer-total").textContent = `${cartTotalPrice}`;
});

async function getCart() {
  const response = await fetch("/cart.js", {
    method: "GET",
    credentials: "same-origin",
    headers: {
      "Content-Type": "application/json",
    },
  });

  return await response.json();
}

async function addProductToCart(id) {
  if (!id) return;

  return await fetch("/cart/add.js", {
    method: "POST",
    credentials: "same-origin",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      items: [
        {
          id: id,
          quantity: 1,
        },
      ],
    }),
  });
}

async function removeProductFromCart(id) {
  if (!id) return;

  return await fetch("/cart/change.js", {
    method: "POST",
    credentials: "same-origin",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      id: id,
      quantity: 0,
    }),
  });
}
