let scalePointInfos = document.querySelectorAll('.scale-point-info')

scalePointInfos.forEach(scalePointInfo => {
  let scalePointInfoText = scalePointInfo.querySelector('.scale-point-info-text');

  if (scalePointInfo) {
    if (window.matchMedia('screen and (max-width: 767px)')) {
      scalePointInfo.addEventListener('click', function (e) {
        e.preventDefault();
        if(!scalePointInfo) return;
        
        if (scalePointInfoText.style.visibility == 'hidden') {
          scalePointInfoText.style.visibility = 'visible';
        } else {
          scalePointInfoText.style.visibility = 'hidden';
        }
      });
    }
  }
})